.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;

  @include flex(flex, center, center);
  overflow-y: scroll;

  background-color: rgba(79, 20, 34, 0.3);
  transition: opacity 250ms var(--cubic), visibility 250ms var(--cubic);

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

// .is-hidden {
//   opacity: 0;
//   visibility: hidden;
//   pointer-events: none;
// }

.backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: unset;
}
